  width: auto;
  padding: 16px;
  margin-top: -22px;
  color: #4286f4;
  font-weight: bold;
  font-size: 22px;
  background: #fff;
  transition: 0.6s ease;
  border-radius: 0 3px 3px 0;
  left: 0;
}

.slideshow-container .next {
  cursor: pointer;
  position: absolute;
  top: 30%;
  width: auto;
  padding: 16px;
  background: #fff;
  margin-top: -22px;
  color: #4286f4;
  font-weight: bold;
  font-size: 22px;
  transition: 0.6s ease;
  border-radius: 0 3px 3px 0;
  right: 0;
}

/* Position the "next button" to the right */
.next {
  right: 0;
  border-radius: 3px 0 0 3px;
}

/* On hover, add a black background color with a little bit see-through */
.prev:hover,
.next:hover {
  background-color: rgba(0, 0, 0, 0.8);
}

/* Caption text */
.text {
  color: #f2f2f2;
  font-size: 15px;
  padding: 8px 12px;
  position: absolute;
  bottom: 8px;
  width: 100%;
  text-align: center;
}

