@media (min-width: 768px) {
  /* show 3 items */
  .carousel-inner .active,
  .carousel-inner .active + .carousel-item,
  .carousel-inner .active + .carousel-item + .carousel-item,
  .carousel-inner .active + .carousel-item + .carousel-item + .carousel-item {
    display: block;
  }

  .carousel-inner
    .carousel-item.active:not(.carousel-item-right):not(.carousel-item-left),
  .carousel-inner
    .carousel-item.active:not(.carousel-item-right):not(.carousel-item-left)
    + .carousel-item,
  .carousel-inner
    .carousel-item.active:not(.carousel-item-right):not(.carousel-item-left)
    + .carousel-item
    + .carousel-item,
  .carousel-inner
    .carousel-item.active:not(.carousel-item-right):not(.carousel-item-left)
    + .carousel-item
    + .carousel-item
    + .carousel-item {
    transition: none;
  }

  .carousel-inner .carousel-item-next,
  .carousel-inner .carousel-item-prev {
    position: relative;
    transform: translate3d(0, 0, 0);
  }

  .carousel-inner
    .active.carousel-item
    + .carousel-item
    + .carousel-item
    + .carousel-item
    + .carousel-item {
    position: absolute;
    top: 0;
    right: -25%;
    z-index: -1;
    display: block;
    visibility: visible;
  }

  /* left or forward direction */
  .active.carousel-item-left + .carousel-item-next.carousel-item-left,
  .carousel-item-next.carousel-item-left + .carousel-item,
  .carousel-item-next.carousel-item-left + .carousel-item + .carousel-item,
  .carousel-item-next.carousel-item-left
    + .carousel-item
    + .carousel-item
    + .carousel-item,
  .carousel-item-next.carousel-item-left
    + .carousel-item
    + .carousel-item
    + .carousel-item
    + .carousel-item {
    position: relative;
    transform: translate3d(0, 0, 0) !important;
    visibility: visible;
  }

  /* farthest right hidden item must be abso position for animations */
  .carousel-inner .carousel-item-prev.carousel-item-right {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    display: block;
    visibility: visible;
  }

  /* right or prev direction */
  .active.carousel-item-right + .carousel-item-prev.carousel-item-right,
  .carousel-item-prev.carousel-item-right + .carousel-item,
  .carousel-item-prev.carousel-item-right + .carousel-item + .carousel-item,
  .carousel-item-prev.carousel-item-right
    + .carousel-item
    + .carousel-item
    + .carousel-item,
  .carousel-item-prev.carousel-item-right
    + .carousel-item
    + .carousel-item
    + .carousel-item
    + .carousel-item {
    position: relative;
    transform: translate3d(0, 0, 0) !important;
    visibility: visible;
    display: block;
    visibility: visible;
  }
}

/* Bootstrap Lightbox using Modal */

#profile-grid {
  overflow: auto;
  white-space: normal;
}
#profile-grid .profile {
  padding-bottom: 40px;
}
#profile-grid .panel {
  padding: 0;
}
#profile-grid .panel-body {
  padding: 15px;
}
#profile-grid .profile-name {
  font-weight: bold;
}
#profile-grid .thumbnail {
  margin-bottom: 6px;
}
#profile-grid .panel-thumbnail {
  overflow: hidden;
}
#profile-grid .img-rounded {
  border-radius: 4px 4px 0 0;
}

a.carousel-control-prev {
  width: 100px;
  height: 100px;
  background: red;
  display: block;
  position: absolute;
}

a.carousel-control-next.text-faded {
  width: 100px;
  height: 100px;
  background: red;
  display: block;
  position: absolute;
  right: 0;
}

#carouselExample .carousel-inner .carousel-item {
  transform: !important;
}

/* loginsignup */
.login-signup {
  position: relative;
}
.login-registeration {
  position: absolute;
  z-index: 10;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  background-color: rgba(0, 0, 0, 0.5);
}

/* form */
.form {
  flex-basis: 30%;
  box-shadow: 0 0 10px 2px rgba(0, 0, 0, 0.1);
  padding: 5%;
  padding-top: 12px;
  padding-bottom: 2%;
  display: grid;
  grid-template-rows: repeat(4, auto);
  grid-gap: 15px;
  background-color: #ffffff;
  border-radius: 15px;
}

.form-div {
  text-align: center;
}

.form-div h2 {
  margin-bottom: 0;
  font-size: 1.3rem;
  color: var(--black);
  font-weight: 600;
  cursor: pointer;
  padding-bottom: 10px;
}
.form-switch {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.form-input {
  display: grid;
  grid-template-columns: 18% 70%;
  justify-content: space-between;
  align-items: center;
}
.form-submit {
  padding-top: 10px;
}
.form-input input {
  border-color: #353b48;
  border-radius: 8px;
  padding: 5px;
  height: 30px;
}
.form-submit input {
  font-family: var(--heading);
  padding: 2%;
  padding-left: 8%;
  padding-right: 8%;
  border-radius: 30px;
  font-size: 1.1rem;
  border: 0;
  border: 2px solid var(--black);
  color: var(--black);
  font-family: var(--heading);
  cursor: pointer;
}

.form-div label {
  font-size: 1rem;
  color: var(--black);
  font-weight: 700;
}
.form-close {
  display: grid;
  justify-content: flex-end;
}
.form-close button {
  background-color: #ffffff;
  border: none;
  font-size: 1.3rem;
  font-weight: 700;
  margin-right: -4vw;
  padding: 5px;
  border: 1px solid white;
}
.form-close button:hover {
  border: 1px solid black;
}
/* cards */
.popular {
  margin-top: 5%;
}
.popular h3 {
  margin-bottom: 5px;
}
/* .search-query */
.search-query {
  background: white;
  z-index: 2;
  width: 100%;
  max-width: 1050px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  box-shadow: 0 0 10px 2px rgba(0, 0, 0, 0.1);
  margin-top: -70px;
}
.search-query ul {
  display: inline-grid;
  width: 95%;
  padding-left: 0;
  padding-bottom: 10px;
}
.search-query ul li {
  display: grid;
  grid-template-columns: auto 1fr auto;
  border-bottom: 1px solid #f39c12;
  padding: 5px;
  cursor: pointer;
  align-items: center;
  grid-gap: 5px;
}
.search-query ul li i {
  font-size: 19px;
  color: #f39c12;
}
.search-query ul li:hover {
  background-color: rgba(0, 0, 0, 0.1);
}
.search-query ul li p {
  margin-bottom: 0;
}
.search-query ul span {
  font-size: 1.3rem;
  font-weight: 900;
}
/* media query */
@media all and (max-width: 1690px) {
  .form {
    flex-basis: 40%;
  }
}
@media all and (max-width: 1199px) {
  .form {
    flex-basis: 50%;
  }
}
@media all and (max-width: 991px) {
  .form {
    flex-basis: 50%;
  }
}
@media all and (max-width: 768px) {
  .form {
    flex-basis: 80%;
  }
}
@media all and (max-width: 480px) {
  .form {
    flex-basis: 96%;
  }
}
